body {
  background-color: rgb(238, 232, 232);
}
.app-wrapper {
  display: grid;
  width: 1600px;
  margin: 0 auto;

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-template-areas: 
    "h h"
    "n c";
  grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: rgba(236, 214, 16, 0.24);
  }